import React from 'react'
import styled from '@emotion/styled'
import { Link } from 'gatsby'
import { FiExternalLink } from 'react-icons/fi'
import { Wrapper } from './wrapper'
import { colors } from './theme'

const Nav = styled.nav`
  height: 60px;
  background: #fff;
`

const NavItems = styled.ul`
  height: 40px;
  display: flex;
  flex-direction: row;
  list-style-type: none;
  align-items: center;
`

const NavItem = styled.li`
  padding: 0.5rem 1rem;

  a {
    text-decoration: none;
    color: ${colors.brand};

    img {
      height: 24px;
      vertical-align: middle;
      border-radius: 50%;
      margin-right: 0.5rem;
    }
  }
`

const NavBrand = styled(NavItem)`
  padding-left: 0px;
`

const ExternalLink = styled(FiExternalLink)`
  color: ${colors.text};
`

export const Navbar = () => (
  <Nav>
    <Wrapper>
      <NavItems>
        <NavBrand>
          <Link to="/">All posts</Link>
        </NavBrand>

        <NavItem>
          <a
            href="https://github.com/mfrachet"
            target="_blank"
            rel="noopener"
            rel="noreferrer"
          >
            Github <ExternalLink />
          </a>
        </NavItem>
        <NavItem>
          <a
            href="https://twitter.com/mfrachet"
            target="_blank"
            rel="noopener"
            rel="noreferrer"
          >
            Twitter <ExternalLink />
          </a>
        </NavItem>
      </NavItems>
    </Wrapper>
  </Nav>
)
